import { ExistenceType } from '../constants/ExistenceType'

export const arrangeRowDataList = (proofDetailList) => {
    var rowList = []
    // each element
    // {
    //     collapse: false,
    //     dataList:[]
    // }
    var currentList = []
    var collapse = false
    proofDetailList.forEach((data, index) => {
        if (index === 0) {
            if (data.existenceType === ExistenceType.EXIST) {
                collapse = false
            } else {
                collapse = true
            }
            currentList.push(data)
        } else {
            // append
            if (data.existenceType === proofDetailList[index - 1].existenceType) {
                currentList.push(data)
            } else {
                // currentListrowListcurrentListcollapse
                rowList.push({
                    collapse: collapse,
                    dataList: currentList,
                })
                currentList = []
                currentList.push(data)
                if (data.existenceType === ExistenceType.EXIST) {
                    collapse = false
                } else {
                    collapse = true
                }
            }
        }
    })
    //, 
    rowList.push({
        collapse: collapse,
        dataList: currentList,
    })
    return rowList
}
